import { useState, useEffect } from "react";
import axios from "axios";
import * as React from 'react';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import heatmap from 'canvas-heatmap';
import Heatmap from "react-simple-heatmap"
import "./dashboard.css"



const Home = (props) => {
  const [lastid, setLastid] = useState(-1);
  const [realdata, setRealdata] = useState([]);
  useEffect(() => {
    const interval = setInterval(() =>
      axios
        .get("https://api.thingspeak.com/channels/1848526/feeds.json?api_key=9DL4JXYDG8C6P32U&results=1")
        // .get("https://api.thingspeak.com/channels/1848526/fields/2.json?api_key=9DL4JXYDG8C6P32U&results=5")
        .then((response) => {
          console.log(response.data.feeds)
          if (response.data.feeds[0].entry_id !== lastid) {
            setLastid(response.data.feeds[0].entry_id)
            realdata.push(response.data.feeds[0])
            setRealdata([...realdata])
          }
        }), 5000)
    return () => clearInterval(interval)
  });
  let x = [1, 2, 3, 4, 5, 6, 7, 8]
  let y = [1, 2, 3, 4, 5, 6, 7, 8]
  let z = [[25.50, 25.50, 26.25, 25.50, 26.25, 26.75, 28.75, 29.75],
  [25.50, 26.25, 25.50, 26.75, 27.00, 26.25, 27.75, 29.25],
  [26.50, 27.25, 25.75, 26.25, 26.25, 25.50, 26.50, 29.00],
  [25.75, 27.50, 26.50, 25.75, 25.75, 25.25, 25.50, 26.50],
  [26.00, 26.00, 27.00, 25.75, 25.50, 25.50, 25.00, 25.75],
  [25.00, 25.50, 25.00, 25.75, 26.00, 26.25, 25.75, 26.50],
  [25.75, 25.50, 25.25, 25.25, 25.50, 25.75, 26.50, 26.75],
  [25.00, 25.75, 25.50, 25.50, 25.75, 26.25, 26.25, 26.25]]
  var data = { x, y, z };
  var colors = [
    { color: "#000080", point: 0 },
    { color: "#3366FF", point: 0.142857142857143 },
    { color: "#00B0DC", point: 0.285714285714286 },
    { color: "#009933", point: 0.428571428571429 },
    { color: "#FFFF5B", point: 0.571428571428571 },
    { color: "#E63300", point: 0.714285714285714 },
    { color: "#CC0000", point: 0.857142857142857 },
    { color: "#800000", point: 1 },
  ];
  var options = {
    colors,
    thresholdStep: 19,
    xLabel: "x-axis",
    yLabel: "y-axis",
    zLabel: "z-axis",
    fontSize: 15,
    zMin: 0,
  };
  heatmap("heatmap", data, options)
  // console.log(realdata.map((row)=>{return row.entry_id}))
  return (
    <>
      <div className="titlebar">ESW Dashboard</div>
      <TableContainer component={Paper}>
        <Table sx={{ minWidth: 650 }} aria-label="simple table">
          <TableHead>
            <TableRow>
              <TableCell>Entry_id</TableCell>
              <TableCell>PIR</TableCell>
              <TableCell>Grid eye 1</TableCell>
              <TableCell>Grid eye 2</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {realdata.map((row) => (
              <TableRow
                key={row.entry_id}
                sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
              >
                <TableCell component="th" scope="row">
                  {row.entry_id}
                </TableCell>
                <TableCell component="th" scope="row">
                  {row.field1}
                </TableCell>
                <TableCell component="th" scope="row">
                  {row.field2}
                </TableCell>
                <TableCell component="th" scope="row">
                  {row.field3}
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </>
  );
};

export default Home;